import React, {useEffect, useState} from 'react'
import {observer} from 'mobx-react-lite'
import taskContentStore from "../../../../../lib/store/task-content-store";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {nanoid} from "nanoid";
import { faTrashCan} from "@fortawesome/free-solid-svg-icons";
import taskScheduleStore from "../../../../../lib/store/task-schedule-store";


export default observer(() => {
    const [valueInput, setValueInput] = useState('')
    const [valueTextarea, setValueTextarea] = useState('')
    useEffect(() => {
        setValueInput(taskContentStore.taskTitle)
        setValueTextarea(taskContentStore.taskBody)
    }, [])
    const onChangeValueInput = (e: any) => {
        setValueInput(e.target.value)
    }
    const onChangeValueTextarea = (e: any) => {
        setValueTextarea(e.target.value)
    }
    const open = () => {
        taskScheduleStore.setStateModal(true)
        console.log(taskScheduleStore.stateModal)
    }

    return (
        <div className='task-content-body'>
            <div className='task-content-body-title w-full'>
                <input autoFocus={true} value={valueInput} onChange={onChangeValueInput} type="text"
                       placeholder="Type here"
                       className="input-title input input-ghost w-full "/>
            </div>
            <div className='task-content-body-text'>
                <textarea value={valueTextarea} onChange={onChangeValueTextarea}
                          className="task-content-body-text-textarea textarea textarea-ghost w-full"
                          placeholder="Bio"/>
            </div>

            <label htmlFor="my-modal" onClick={open} className="btn btn-delete no-animation btn-ghost">
                <FontAwesomeIcon
                    className='icon-delete' key={nanoid()}
                    icon={faTrashCan}/>
            </label>

        </div>

    )
})
