import React, {useEffect} from 'react'
import {observer} from 'mobx-react-lite'
import {nanoid} from "nanoid";
import {faStopwatch} from "@fortawesome/free-solid-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import './DropdownTimers.scss'
import dropdownTimerStore from "../../../../lib/store/dropdown-timer-store";
// @ts-ignore
import pop from "../../Music/pop.wav";
import taskContentStore from "../../../../lib/store/task-content-store";


export default observer(() => {
    const onChangeTime = (event: any):void => {
        dropdownTimerStore.setValue(event.target.value)
    }

    const timer = (): void => {
        taskContentStore.setCheckbox(!taskContentStore.checkbox)
    }
    useEffect((): void => {
        if (taskContentStore.checkbox) {
            dropdownTimerStore.setValue("00:15:00")
        } else if (!taskContentStore.checkbox) {
            dropdownTimerStore.setValue("00:00:00")
        }
    }, [taskContentStore.checkbox])

    return (
        <div className="dropdown  dropdown-end  dropdown-timers">
            <label tabIndex={0} className="btn m-1 btn-stopwatch btn-ghost"> <FontAwesomeIcon key={nanoid()}
                                                                                              icon={faStopwatch}/></label>
            <div tabIndex={0} className="dropdown-content card card-compact w-64 shadow bg-base-100 ">
                <div className='dropdown-timers-deadline'>
                    
                    <div>
                        <input value={dropdownTimerStore.value} onChange={onChangeTime} type="time"
                               placeholder="Type here"
                               className="input w-full max-w-xs" step={2}/>
                    </div>
                </div>
                <div className='dropdown-timers-deadline'>
                      15  <input checked={taskContentStore.checkbox} onClick={timer}
                                                 type="checkbox"
                                                 className="toggle toggle-warning dropdown-timers-deadline-toggle"/>
                </div>

            </div>
        </div>
    )
})
