import React, {useEffect, useState} from 'react'
import {observer} from 'mobx-react-lite'
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {nanoid} from "nanoid";
import {faPlus} from "@fortawesome/free-solid-svg-icons";
import taskScheduleStore from "../../../../lib/store/task-schedule-store";

interface Interface {
    onChange: any
    val: any
}

export default observer(({onChange, val}: Interface) => {
    const [newArray, setNewArray] = useState({})

    const addArray = () => {
        setNewArray({
            folder: 0,//
            category: taskScheduleStore.stateBtnCategory,
            id: 0,
            date: taskScheduleStore.nowDate,
            task_title: ' ',
            task_body: ''
        })
    }
    useEffect(() => {
       val.push(newArray)
    }, [newArray])
    return (

        <a onClick={addArray} className="btn btn-warning home-search-form-btn">
            <FontAwesomeIcon className='icon-plus' key={nanoid()}
                             icon={faPlus}/>
        </a>


    )
})
