import React, {useState} from 'react'
import {observer} from 'mobx-react-lite';
import './CustomCard.scss'
import taskScheduleStore from "../../../lib/store/task-schedule-store";
import taskContentStore from "../../../lib/store/task-content-store";

interface Interface {
    data: any
}

export default observer(({data}: Interface) => {
    const [stateCheck, setStateCheck] = useState(false)

    const changeStateTaskContent = (): void => {
        taskScheduleStore.setStateTaskContent(true)
        taskContentStore.setTaskBody(data.task_body)
        taskContentStore.setTaskTitle(data.task_title)
        taskContentStore.setDate(data.date)
        taskContentStore.setTaskCategory(data.category)
    }

    return (
        <div
            style={data.category === 1 ?
                {background: '#F89705'}
                :
                data.category === 2 ?
                    {background: '#5496FB'}
                    :
                    data.category === 3 ?
                        {background: '#C262DA'} :
                        {background: '#ffffff'}
            }
            className="w-full  custom-card">
            <div className="w-full bg-base-100 custom-card-pre">
                <input onClick={() => setStateCheck(!stateCheck)} type='checkbox' checked={stateCheck}
                       className="checkbox checkbox-secondary"/>
                <div onClick={changeStateTaskContent} className="custom-card-pre-text"
                     style={stateCheck ? { color: '#8F9399'} : {}}>
                    {data.task_title}
                </div>
            </div>
        </div>
    )
})
