import searchFormStore from "./store/search-form-store";
import {Category} from "./models/category";


export const FilterVal = (Array: Category[]) => {
    // console.log(Array)
    return Array.filter((val) => {
        return val.task_title.toLowerCase().includes(searchFormStore.value.toLowerCase())
    })
}

export const countdown = (value: string): number => {
    const timeParts = value.split(":");
    return (+timeParts[0] * 60 * 60 + +timeParts[1] * 60 + +timeParts[2]);
}